.appWrapper{
  display: grid;
  width:1200px;
  margin: 0 auto;
  grid-template-rows: 60px 1fr;
  grid-template-columns:2fr 10fr ;

  grid-template-areas:
          'h h'
          'nav c';

}
.appWrapperContent{
  grid-area: c;
  background-color: lemonchiffon;
  padding: 20px;
}

